import React, {useState, useEffect} from "react";
import axios from "axios";
import Button from '@material-ui/core/Button';
const NewContact = () =>{
    const [items, setItems] = useState([]);
    const [num, setNum] = useState(1);
    const [itemName, setItemName] = useState("");
    const [image, setImage] = useState([]);
   
 console.log(num);
// useEffect(() => {
    async function getData(){
        const res = await  axios.get(`https://pokeapi.co/api/v2/pokemon/${num}`);
        console.log(res.data.sprites.back_default);
        setImage(res.data.sprites.back_default)
      }
     
//});



const addItem = event => {
    event.preventDefault();
    setItems([
       ...items,
      {
        id: items.length,
        name: itemName,
        imgsrc: image
      }
    ]);
    console.log(items.length);
    items.length == 0? setNum(1):setNum(items.length + 1);
    setItemName("");
    getData();
  };

    return (
      <>
        <form onSubmit={addItem}>
        <h1>Select Pokemon here  </h1>
          <label>
            <input
              name="item"
              type="text"
              placeholder="Add Pokemon Name"
              value={itemName}
              onChange={e => setItemName(e.target.value)}
            />
            <Button variant="contained" color="primary" type="submit">Add Pokemon</Button>
          
          </label>
        </form>
        <div className="pokemon_list">
        <ul className="setlist ">
          {items.map(item => (
                        <li key={item.id} data-id={item.id}>
            <p><img src={item.imgsrc} /></p>
            <p>{item.name}</p></li>
          ))}
        </ul>
        </div>
      </>
    );

}
export default NewContact;