import React from "react";
import Card from "./Card";
import Sdata from './Sdata';
const About = () => {

    return(<>
    <div className="abouts__page">
<h2 className="about">About </h2>
<p className="about">
In the previous example, the 
element had only one tag and didn’t
 include a closing tag. Fear not, this was intentional. Not all elements consist of opening and closing tags. Some elements simply receive their content or behavior from attributes within a single tag. The
  element is one of these elements. The content of the previous 
   element is assigned with the use of the charset attribute and value.
 Other common selfclosing elements include
</p>

<ul className="setlist">{Sdata.map((val, index) =>{

return(<Card 
imgsrcq={val.imgscr}
titleq={val.title}
/>)
})}
</ul>
</div>
    </>)
}
export default About;